<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="m_menu">
      <van-cell :title="item.title" is-link :to="item.path" v-for="item in list"/>
    </div>

  </div>
</template>

<script>
  export default {
    name: "gtja",
    data(){
      return {
        title:'国泰君安',
        list: [
          {title: '利率互换', path: '/mall/gtja/interestRate'},
          {title: '信用违约互换', path: '/mall/gtja/creditDefaultSwap'},
          {title: '结构金融日报', path: '/mall/gtja/financialTimes'},
          {title: '业务人员联系方式', path: '/mall/gtja/linked'},
          {title: '免责声明', path: '/mall/gtja/declare'},
          {title: '运营管理', path: '/mall/gtja/operation'}
        ],
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      }
    }
  }
</script>

<style scoped>

</style>



